.nWrapper {
  height: 10vh;
  display: flex;
  justify-content: space-between;
}

.nLeft {
  flex: 1;
  align-items: center;
  display: flex;
  gap: 2rem;
}

.nName {
  font-size: 1.3rem;
  font-weight: bold;
}

.nRight {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  font-weight: 400;
}

.nList {
  flex: 10;
}

.nList>ul {
  display: flex;
  gap: 2rem;
  margin-right: 4rem;
}

.nList>ul>li:hover {
  cursor: pointer;
  color: var(--orange);
}

.button {
  border-radius: 34px;
  border: none;
  color: white;
  font-size: 16px;
  padding: 11px 26px 11px 26px;
  background: linear-gradient(180deg, #fdc50f 26.71%, #fb982f 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.42);
}

.nButton {
  flex: 2;
}

:root {
  --yellow: #F5C32C;
  --orange: #FCA61F;
  --black: #242D49;
  --gray: #788097;
  --blueCard: #DDF8FE;
  --purple: rgb(238 210 255);
  --boxShadow: 0px 19px 60px rgb(0 0 0 / 8%);
  --orangeCard: rgba(252, 166, 31, 0.45);
  --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App {
  padding: 0.5rem 3.5rem;
  overflow: hidden;
  color: var(--black);
}

.button {
  border-radius: 34px;
  // bze: 16px;
